import {Component, HostListener, Inject, OnInit} from '@angular/core';
import {DoRequestService} from '../services/do-request.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import { TokenService } from '../services/token.service';
import {environment} from '../../environments/environment';
import {ResizeService} from '../resizing-logic/resizing.service';
import {delay} from 'rxjs/operators';
import {SCREEN_SIZE} from '../resizing-logic/screen-size.enum';
import {IsLoggedInBoolService} from '../services/IsLoggedInBoolService';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.component.html',
  styleUrls: ['./login-modal.component.css']
})
export class LoginModalComponent implements OnInit {
  baseUrl: string;
  public innerWidth: any;
  size: SCREEN_SIZE;
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
  }

  isUserLoggedIn: boolean;
  constructor(private doRequest: DoRequestService,
              private snackBar: MatSnackBar,
              public dialogRef: MatDialogRef<LoginModalComponent>,
              public tokenService: TokenService,private resizeSvc: ResizeService,
              private isLoggedInBoolService: IsLoggedInBoolService,
              private router: Router) {
    isLoggedInBoolService.myBool$.subscribe((newBool: boolean) => { this.isUserLoggedIn = newBool; });
    this.baseUrl = environment.apiUrlBase;
    this.resizeSvc.onResize$
      .pipe(delay(0))
      .subscribe(x => {
        this.size = x;
      });
  }


  user = {
    email : '',
    password: '',
    remember: ''
  };

  newUser = {
    email : '',
    password : '',
    name : '',
    phone : '',
    password_confirmation : '',
    terms_and_conditions : ''
  };

  error = {
    message : '',
    errors : {
      email : '',
      password : '',
      name : '',
      phone : ''
    }
  };

  errorNewUser = {
    message : '',
    errors : {
      email : '',
      password : '',
      name : '',
      phone : '',
      terms_and_conditions : ''
    }
  };
  isSendingRequest = false;
  isError = false;
  signIn = true;
  signUp = false;

  ngOnInit(): void {
    window.dispatchEvent(new Event('resize'));
  }

  showSignInTab() {
    this.signIn = true;
    this.signUp = false;
  }

  showSignUpTab() {
    this.signIn = false;
    this.signUp = true;
  }
  login(): void {
    this.isSendingRequest = true;
    this.doRequest.login().subscribe(data => {
      this.doRequest.performLogin(this.user).subscribe(res => {
        this.isLoggedInBoolService.myBool(true);
        this.tokenService.setUser(res);
        this.tokenService.setIsLoggedIn();
        this.isSendingRequest = false;
        this.dialogRef.close('isLoggedIn');
        this.snackBar.open('You have been successfully logged in!', 'Close', {
          duration: 5000,
        });
      }, err => {
        console.log(err);
        this.isSendingRequest = false;
        this.error.message = err.error.message;
        if (err.error.errors) {
          this.error.errors = err.error.errors;
        }
      });

    }, err => {
    }, () => {

    });
  }


  register(): void {
    this.isSendingRequest = true;
    this.isError = false;
    this.doRequest.login().subscribe(data => {
      this.doRequest.register(this.newUser).subscribe(res => {
        this.isSendingRequest = false;
        this.dialogRef.close();
        this.snackBar.open('You have been successfully registered, please log in!', 'Close', {
          duration: 5000,
        });
      }, err => {
        this.isSendingRequest = false;
        this.isError = true;
        this.errorNewUser.message = err.error.message;
        if (err.error.errors) {
          this.errorNewUser.errors = err.error.errors;
        }
      });

    }, err => {
      console.log(err);
    }, () => {

    });
  }

  forgotPassword(){
    this.router.navigate(['/forgot-password']);
    this.dialogRef.close();
  }
}
