/* tslint:disable */
import {Component, HostListener, OnInit} from '@angular/core';
import {DoRequestService} from '../services/do-request.service';
import { Router } from '@angular/router';
import { TokenService } from '../services/token.service';
import {SCREEN_SIZE} from '../resizing-logic/screen-size.enum';
import {ResizeService} from '../resizing-logic/resizing.service';
import {delay} from 'rxjs/operators';
import {environment} from '../../environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  user = {
    email : '',
    password: '',
    remember: ''
  };

  newUser = {
    email : '',
    password : '',
    name : '',
    phone : '',
    password_confirmation : '',
    terms_and_conditions : ''
  };

  error = {
    message : '',
    errors : {
      email : '',
      password : '',
      name : '',
      phone : ''
    }
  };

  errorNewUser = {
    message : '',
    errors : {
      email : '',
      password : '',
      name : '',
      phone : '',
      terms_and_conditions : ''
    }
  };
  isSendingRequest = false;
  signIn = true;
  signUp = false;
  baseUrl: string;
  public innerWidth: any;
  size: SCREEN_SIZE;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
  }

  constructor(private doRequest:
    DoRequestService,
              private router: Router,
              private tokenService: TokenService,
              private resizeSvc: ResizeService) {
      this.baseUrl = environment.apiUrlBase;
    this.resizeSvc.onResize$
      .pipe(delay(0))
      .subscribe(x => {
        this.size = x;
        if (this.size <= 1) {

        }
      });
    }

  ngOnInit(): void {

  }

  showSignInTab() {
    this.signIn = true;
    this.signUp = false;
  }

  showSignUpTab() {
    this.signIn = false;
    this.signUp = true;
  }
  login(): void {
    this.isSendingRequest = true;
    this.doRequest.login().subscribe(data => {
      this.doRequest.performLogin(this.user).subscribe(res => {
        const navigateBackUrl = localStorage.getItem('nagiateBackUrl');
        if (navigateBackUrl) {
          localStorage.removeItem('nagiateBackUrl');
          this.router.navigate([navigateBackUrl]);
        } else {
          this.router.navigate(['/']);
        }
        this.isSendingRequest = false;
      }, err => {
        this.isSendingRequest = false;
        console.log(err.error.message);
        this.error.message = err.error.message;
        if (err.error.errors) {
          this.error.errors = err.error.errors;
        }
      });

    }, err => {
      console.log(err);
    }, () => {

    });
  }


  register(): void {
    this.isSendingRequest = true;
    this.doRequest.login().subscribe(data => {
      this.doRequest.register(this.newUser).subscribe(res => {
        this.isSendingRequest = false;
      }, err => {
        this.isSendingRequest = false;
        console.log(err.error.message);
        this.errorNewUser.message = err.error.message;
        if (err.error.errors) {
          this.errorNewUser.errors = err.error.errors;
        }
      });

    }, err => {
      console.log(err);
    }, () => {

    });
  }}
