import { Component, OnInit } from '@angular/core';
import {DoRequestService} from '../../services/do-request.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-send-password-forget-email',
  templateUrl: './send-password-forget-email.component.html',
  styleUrls: ['./send-password-forget-email.component.css']
})
export class SendPasswordForgetEmailComponent implements OnInit {

  isLoading = false;
  email = '';
  message:string;
  isEmailPresent:boolean;
  emailIsInvalid = true;
  showForm = true;
  constructor(private doRequest:DoRequestService,
              private router: Router) { }

  ngOnInit(): void {
  }

  sendPasswordResetLink() {
    this.isLoading = true;

    if(this.isValidateEmail(this.email)){
      this.emailIsInvalid = true;
      this.doRequest.sendPasswordResetLink({email: this.email}).subscribe( res => {
        this.message = res.message;
        this.isEmailPresent = res.isEmailPresent;
        this.isLoading = false;
        this.showForm = false;
      }, err => {
        console.log(err);
        this.isLoading = false;
      })
    }else {
      this.emailIsInvalid = false
      this.isLoading = false;
    }
  }

  isValidateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }

  goToHome() {
    this.router.navigate(['/']);
  }

}
