import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { DoRequestService } from '../services/do-request.service';
import { TokenService } from '../services/token.service';
import {IsLoggedInBoolService} from '../services/IsLoggedInBoolService';

@Injectable({
  providedIn: 'root'
})
export class CheckLoginGuard implements CanActivate {

  constructor(private router: Router,
              private doRequest: DoRequestService,
              private tokenService: TokenService,
              private isLoggedInBoolService: IsLoggedInBoolService) { }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

    // using a promise waits for an asyncronous request to return a value
    return new Promise((resolve , reject) => {
      this.doRequest.login().subscribe( res => {

        this.doRequest.getUser().subscribe( r => {
          this.router.navigate(['/']);
          this.isLoggedInBoolService.myBool(true);
        }, err => {
          this.isLoggedInBoolService.myBool(false);
          resolve(true);
        });

      }, err => {
        console.log(err);
        this.router.navigate(['/login']);

        resolve(false);
      });
    });

  }
}
