import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import {DoRequestService} from '../../services/do-request.service';
import {ProductColor} from '../../models/ProductColor';
import {ProductType} from '../../models/ProductType';
import {ProductBrand} from '../../models/ProductBrand';
import {ProductCondition} from '../../models/ProductCondition';
import {MatDialog} from '@angular/material/dialog';
import {LoginModalComponent} from '../../login-modal/login-modal.component';
import {MatSnackBar} from '@angular/material/snack-bar';
import {ProductAddSuccessModalComponent} from '../product-add-success-modal/product-add-success-modal.component';
import { HttpClient } from '@angular/common/http';
import { SCREEN_SIZE } from '../../resizing-logic/screen-size.enum';
import { ResizeService } from '../../resizing-logic/resizing.service';
import { delay } from 'rxjs/operators';
import {environment} from '../../../environments/environment';
import {IsLoggedInBoolService} from '../../services/IsLoggedInBoolService';
import { NgxImageCompressService } from 'ngx-image-compress';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
declare var $: any;
@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css']
})
export class AddProductComponent implements OnInit {
   sizeOfOriginalImage: number;
   localCompressedURl: string;
   sizeOFCompressedImage: number;

  constructor(private doRequest: DoRequestService,
              public dialog: MatDialog, private snackBar: MatSnackBar,
              private http: HttpClient,
              private resizeSvc: ResizeService,
              private imageCompress: NgxImageCompressService,
              private isLoggedInBoolService: IsLoggedInBoolService,
              private modalService: NgbModal,
              private formBuilder: FormBuilder) {
    this.isLoggedInBoolService.myBool$.subscribe((newBool: boolean) => {
      this.isUserLoggedIn = newBool;
    });
    this.baseUrl = environment.apiUrlBase;
    this.resizeSvc.onResize$
    .pipe(delay(0))
    .subscribe(x => {
      this.size = x;
    });
  }

  classApplied = false;
  product = {
    title : '',
    city : '',
    condition : '',
    type : '',
    year : '',
    brand : '',
    color : '',
    street : '',
    price : '',
    is_price_negotiable : '',
    can_accept_exchange : '',
    description : ''
  };

  error = {
    message : '',
    errors : {
      brand : '',
      color : '',
      condition : '',
      description : '',
      price : '',
      title : '',
      type : '',
      year : '',
      city : '',
      product_image : ''
    }
  };


  isSendingRequest = false;
  colors: ProductColor[];
  types: ProductType[];
  brands: ProductBrand[];
  conditions: ProductCondition[];
  selectedFile: File[] = null;
  fileTypeError = '';
  private baseUrl = '';
  previewImgPath1: any;
  previewImgPath2: any;
  previewImgPath3: any;
  previewImgPath4: any;
  previewImgPath5: any;
  arrayOfImages = new Array(5);
  myFiles: any [] = [];
  imagePath: any;
  imgURL: string | ArrayBuffer;
  imagePreviews = [];
  public innerWidth: any;
  size: SCREEN_SIZE;
  yearArray = [];

  @ViewChild('fileInput1')
  myInputVariable: ElementRef;
  @HostListener('window:resize', ['$event'])
    onResize(event) {
    this.innerWidth = window.innerWidth;
  }

  isUserLoggedIn: boolean;
  registerForm: FormGroup;
  submitted = false;

  ngOnInit(): void {
    this.doRequest.getUser().subscribe( r => {
      this.isUserLoggedIn = true;
    }, err => {
      this.isUserLoggedIn = false;
    });
    this.yearLoop();
    this.initProductFeatures();

    this.registerForm = this.formBuilder.group({
      title: ['', Validators.required],
      city: ['', Validators.required],
      condition: ['', Validators.required],
      type: ['', Validators.required],
      year: ['', Validators.required],
      brand: ['', Validators.required],
      color: ['', Validators.required],
      street: ['', Validators.required],
      price: ['', Validators.required],
      is_price_negotiable: [false],
      can_accept_exchange: [false],
      description: ['', [Validators.required, Validators.minLength(50)]],
      product_image : ['', Validators.required]
    });
  }

  get f() { return this.registerForm.controls; }


  toggleNav(): void {
    // this.classApplied = !this.classApplied;
    console.log(this.classApplied);
  }

  onFileSelected(event) {

    this.myFiles = [];
    this.imagePreviews = [];

    if ( event.target.files.length ) {
      console.log('image selected');
    } else {
      alert('please select an least 1 image');
      this.myFiles = [];
      this.imagePreviews = [];
      return;
    }

    for (let i = 0; i < event.target.files.length; i++) {
      const mimeType = event.target.files[i].type;
      if (mimeType.match(/image\/*/) == null) {
      alert('please select only images');
      // document.getElementById('product-image1').value = null;
      this.myInputVariable.nativeElement.value = '';
      this.myFiles = [];
      this.imagePreviews = [];
      break;
    }
  }

    // tslint:disable-next-line:prefer-for-of
    for (let i = 0; i < event.target.files.length; i++) {

      const reader = new FileReader();

      reader.onload = (_event) => {

        this.compressFile(_event.target.result,event.target.files[i]['name']);

        this.imgURL = reader.result;
        this.imagePreviews.push(this.imgURL);
      };
      reader.readAsDataURL(event.target.files[i]);
      //this.myFiles.push(event.target.files[i])
  }
}

  compressFile(image,fileName) {
    var orientation = -1;
    this.imageCompress.compressFile(image, orientation, 50, 50).then(
      result => {
        console.warn('Size in bytes is now:', this.imageCompress.byteCount(result));
        const imageBlob = this.dataURItoBlob(result.split(',')[1]);
        const imageFile = new File([imageBlob], fileName, { type: 'image/jpeg' });
        this.myFiles.push(imageFile);
      });}

  dataURItoBlob(dataURI) {
    const byteString = window.atob(dataURI);
    const arrayBuffer = new ArrayBuffer(byteString.length);
    const int8Array = new Uint8Array(arrayBuffer);
    for (let i = 0; i < byteString.length; i++) {
      int8Array[i] = byteString.charCodeAt(i);
    }
    const blob = new Blob([int8Array], { type: 'image/jpeg' });
    return blob;
  }

addProduct() {

    this.submitted = true;

  if (this.registerForm.invalid) {
    alert('Please fill in all fields');
    return;
  }else {
    if(this.isUserLoggedIn){

      console.log(this.myFiles);

      //console.log(this.myFilesCompressed);
      const fd = new FormData();
      // tslint:disable-next-line:prefer-for-of
      for (let i = 0; i < this.myFiles.length; i++) {
        fd.append('product_image[]', this.myFiles[i]);
      }
      fd.append('city', this.f.city.value);
      fd.append('condition', this.f.condition.value);
      fd.append('type', this.f.type.value);
      fd.append('color', this.f.color.value);
      fd.append('brand', this.f.brand.value);
      fd.append('year', this.f.year.value);
      fd.append('price', this.f.price.value);
      fd.append('street', this.f.price.value);
      fd.append('is_price_negotiable', this.f.is_price_negotiable.value);
      fd.append('can_accept_exchange', this.f.can_accept_exchange.value);
      fd.append('description', this.f.description.value);
      fd.append('title', this.f.title.value);


      if (this.myFiles.length) {
        this.isSendingRequest = true;
        this.http.post(`${this.baseUrl}/api/addProduct`, fd , { withCredentials: true }).subscribe( res => {
          this.myFiles = [];
          this.clearAddProductForm();
          this.isSendingRequest = false;
          // document.getElementById('product-image1').value = null;
          this.myInputVariable.nativeElement.value = '';
          this.imagePreviews = [];
          console.log(res);
          this.dialog.open(ProductAddSuccessModalComponent, { data: { product: res }, width: '1000px'});
        }, err => {

          this.isSendingRequest = false;
          console.log(err);
          this.error.message = err.error.message;
          if (err.error.errors) {
            this.error.errors = err.error.errors;
          }

          if (err.status === 401 || err.status === 403 || err.status === 419) {

            this.snackBar.open('You need to be logged in to add a product!', 'Close', {
              duration: 5000,
            });

            const dialogRef = this.dialog.open(LoginModalComponent, {
              width: '800px',
              panelClass: 'custom-dialog-container'
            });
          }

        }, () => {
          this.isSendingRequest = false;
        });
      } else {
        alert('please select at least one image');
      }
    }else {

      const dialogRef = this.dialog.open(LoginModalComponent, {
        width: '800px',
        panelClass: 'custom-dialog-container'
      });
    }
  }

  }

logout() {
    this.doRequest.logout().subscribe( res => {
      console.log(res);
    }, err => {
      console.log(err);
    });
  }

initProductFeatures() {
    this.doRequest.getProductColors().subscribe( res => {
      console.log(res);
      this.colors = res;
    }, err => {
      console.log(err);
    });

    this.doRequest.getProductBrands().subscribe( res => {
      this.brands = res;
    }, err => {
      console.log(err);
    });

    this.doRequest.getProductConditions().subscribe( res => {
      this.conditions = res;
    }, err => {
      console.log(err);
    });

    this.doRequest.getProductTypes().subscribe( res => {
      this.types = res;
    }, err => {
      console.log(err);
    });
  }

clearAddProductForm() {
    this.product = {
      title : '',
      city : '',
      condition : '',
      type : '',
      year : '',
      brand : '',
      color : '',
      street : '',
      price : '',
      is_price_negotiable : '',
      can_accept_exchange : '',
      description : ''
    };
  }

 yearLoop() {
    for (let i = (new Date()).getFullYear(); i >= 2000; i--){
      this.yearArray.push(i);
    }
 }
}
