import { Component, OnInit } from '@angular/core';
import {DoRequestService} from '../../services/do-request.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  passwordObj = {
    password : '',
    password_confirmation : '',
    email : '',
    code : ''
  }

  error = {
    message : '',
    errors : {
      password: '',
      password_confirmation: ''
    }
  }
  message: string;
  isError: boolean;
  isLoading = false;

  constructor(private doRequest:DoRequestService,
              private router: Router,
              private route: ActivatedRoute) { }

  ngOnInit(): void {
    let email = this.route.snapshot.paramMap.get('email');
    let code = this.route.snapshot.paramMap.get('code');

    this.passwordObj.email = email;
    this.passwordObj.code = code;

  }

  goToHome() {
    this.router.navigate(['/']);
  }

  resetPassword() {
    this.resetErrors();
    this.isLoading = true;
    this.doRequest.resetPassword(this.passwordObj).subscribe( res => {
      console.log(res);
      this.isLoading = false;
      this.message = res.message;
      this.isError = res.isError;

      this.passwordObj.password_confirmation = '';
      this.passwordObj.password = '';
    }, err => {
      this.isLoading = false;
      if(err.status == 422){
        this.error = err.error;
      }
    })
  }

  resetErrors() {
    this.error = {
      message : '',
      errors : {
        password: '',
        password_confirmation: ''
      }
    }
  }
}
