import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {BlogComponent} from './blog/blog.component';
import {AddProductComponent} from './product/add-product/add-product.component';
import {LoginComponent} from './login/login.component';
import { SingleProductListingComponent } from './product/single-product-listing/single-product-listing.component';
import { UserComponent } from './user/user/user.component';
import { UserDetailsComponent } from './user/user-details/user-details.component';
import { UserAnnouncementsComponent } from './user/user-announcements/user-announcements.component';
import { UserFavouritesComponent } from './user/user-favourites/user-favourites.component';
import { UserMessagesComponent } from './user/user-messages/user-messages.component';
import { UserNotificationsComponent } from './user/user-notifications/user-notifications.component';
import { SearchComponent } from './search/search.component';
import { AuthGuard } from './guards/auth.guard';
import { EditProductComponent } from './product/edit-product/edit-product.component';
import { SecureGuard } from './guards/secure.guard';
import { RefreshComponent } from './refresh/refresh.component';
import {HomeComponent} from './home/home.component';
import { PostComponent } from './blog/post/post.component';
import {CheckLoginGuard} from './guards/check-login.guard';
import {NotFoundComponent} from './segments/not-found/not-found.component';
import {SellerProfileComponent} from './seller-profile/seller-profile.component';
import {GetVerificationCodeComponent} from './get-verification-code/get-verification-code.component';
import {SendPasswordForgetEmailComponent} from './password/send-password-forget-email/send-password-forget-email.component';
import {ResetPasswordComponent} from './password/reset-password/reset-password.component';
import {FooterComponent} from './segments/footer/footer.component';


const routes: Routes = [
  { path: 'blog', component: BlogComponent },
  { path: 'search-result', component: SearchComponent, canActivate: [AuthGuard]},
  { path: '', component: HomeComponent, canActivate: [AuthGuard]},
  { path: 'add-product', component: AddProductComponent, canActivate: [AuthGuard] },
  { path: 'login', component: LoginComponent, canActivate: [CheckLoginGuard] },
  { path: 'refresh', component: RefreshComponent, canActivate: [AuthGuard] },
  { path: 'forgot-password', component: SendPasswordForgetEmailComponent, canActivate: [AuthGuard] },
  { path: 'reset-password/:email/:code', component: ResetPasswordComponent, canActivate: [AuthGuard] },
  { path: 'single-product-listing/:id', component: SingleProductListingComponent, canActivate: [AuthGuard]},
  { path: 'post/:id', component: PostComponent, canActivate: [AuthGuard]},
  /*{ path: 'edit-product/:id', component: EditProductComponent, canActivate: [AuthGuard]},*/
  { path: 'footer', component: FooterComponent, canActivate: [AuthGuard]},
  { path: 'seller/:id', component: SellerProfileComponent, canActivate: [AuthGuard]},
  { path: 'edit-product/:id', component: EditProductComponent, canActivate: [AuthGuard]},
  { path: 'verify/email/:email/:code', component: GetVerificationCodeComponent, canActivate: [AuthGuard]},
  { path: 'user' , component: UserComponent ,
    children: [
      {path: '', redirectTo: 'announcements', pathMatch: 'full'},
      {path: 'account', component: UserDetailsComponent},
      {path: 'announcements', component: UserAnnouncementsComponent},
      {path: 'favourites', component: UserFavouritesComponent},
      {path: 'messages', component: UserMessagesComponent},
      {path: 'notifications', component: UserNotificationsComponent},
      {path: 'edit-product/:id', component: EditProductComponent},
    ], canActivate: [SecureGuard],
  },
  {path: '404', component: NotFoundComponent},
  {path: '**', component: NotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
