/* tslint:disable:no-trailing-whitespace */
import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {ProductColor} from '../models/ProductColor';
import {ProductBrand} from '../models/ProductBrand';
import {ProductCondition} from '../models/ProductCondition';
import {ProductType} from '../models/ProductType';
import {environment} from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DoRequestService {

  private baseUrl = '';
  private httpOptions = {withCredentials: true};

  constructor(private http: HttpClient) {
    this.baseUrl = environment.apiUrlBase;
  }

  login() {
    return this.http.get(`${this.baseUrl}/sanctum/csrf-cookie`, { withCredentials: true });
  }

  performLogin(data) {
    return this.http.post(`${this.baseUrl}/login`, data, { withCredentials: true });
  }

  logout() {
    return this.http.post(`${this.baseUrl}/logout`, null, { withCredentials: true });
  }

  getUser() {
    return this.http.get(`${this.baseUrl}/api/user`, { withCredentials: true });
  }

  addProduct(product) {
    return this.http.post(`${this.baseUrl}/api/addProduct`, product , { withCredentials: true });
  }

  editProduct(product) {
    return this.http.post(`${this.baseUrl}/api/editProduct`, product , { withCredentials: true });
  }

  getProductBrands() {
    return this.http.get<ProductBrand[]>(`${this.baseUrl}/api/product-brands`);
  }

  getProductColors() {
    return this.http.get<ProductColor[]>(`${this.baseUrl}/api/product-colors`);
  }

  getProductConditions() {
    return this.http.get<ProductCondition[]>(`${this.baseUrl}/api/product-conditions`);
  }

  getProductTypes() {
    return this.http.get<ProductType[]>(`${this.baseUrl}/api/product-types`);
  }

  register(data) {
    return this.http.post(`${this.baseUrl}/api/register`, data, { withCredentials: true });
  }

  getUserDetails() {
    return this.http.get(`${this.baseUrl}/api/getUser`, { withCredentials: true });
  }

  sendEmailToSeller(product) {
    return this.http.post(`${this.baseUrl}/api/sendEmailToSeller`, product , { withCredentials: true });
  }

  removeProductImage(img) {
    return this.http.post(`${this.baseUrl}/api/removeProductImage`, img , { withCredentials: true });
  }

  addFavourited(data) {
    return this.http.post(`${this.baseUrl}/api/favourite`, data , { withCredentials: true });
  }

  performSearch(searchParameters) {
    return this.http.post<any>(`${this.baseUrl}/api/search`, searchParameters , { withCredentials: true });
  }

  getFavourite() {
    return this.http.get<any>(`${this.baseUrl}/api/favourite`, { withCredentials: true });
  }

  getPosts(searchParameters) {
    return this.http.post<any>(`${this.baseUrl}/api/blog`,searchParameters,{ withCredentials: true });
  }

  getSinglePost(postId) {
    return this.http.post<any>(`${this.baseUrl}/api/post`,{id: postId},{ withCredentials: true });
  }

  getSeller(data) {
    return this.http.post<any>(`${this.baseUrl}/api/getSeller`,data,{ withCredentials: true });
  }

  sendMailToSeller(data) {
    return this.http.post<any>(`${this.baseUrl}/api/sendMailToSeller`,data,{ withCredentials: true });
  }

  reportUser(data){
    return this.http.post<any>(`${this.baseUrl}/api/reportUser`,data,{ withCredentials: true });
  }

  addRecentlyViewed(data) {
    return this.http.post<any>(`${this.baseUrl}/api/addRecentlyViewed`,data,{ withCredentials: true });
  }

  getRecentlyViewed() {
    return this.http.post<any>(`${this.baseUrl}/api/getRecentlyViewed`, {},{ withCredentials: true });
  }

  getRelevantToday() {
    return this.http.get<any>(`${this.baseUrl}/api/relevantToday`, { withCredentials: true });
  }

  verifyEmail(data) {
    return this.http.post<any>(`${this.baseUrl}/api/verifyEmail`, data, { withCredentials: true });

  }

  sendPasswordResetLink(data) {
    return this.http.post<any>(`${this.baseUrl}/api/sendPasswordResetLink`, data,{ withCredentials: true });
  }

  resetPassword(data) {
    return this.http.post<any>(`${this.baseUrl}/api/resetPassword`, data,{ withCredentials: true });
  }

  getBlogCategories() {
    return this.http.get<any>(`${this.baseUrl}/api/blog_categories`, { withCredentials: true });
  }
}
