import {Component, HostListener, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import {DoRequestService} from '../../services/do-request.service';
import {environment} from '../../../environments/environment';
import {delay} from 'rxjs/operators';
import {ResizeService} from '../../resizing-logic/resizing.service';
import {SCREEN_SIZE} from '../../resizing-logic/screen-size.enum';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  classApplied = false;
  signedInUser = {
    img_url: '',
    id: '',
    name: ''
  };
  baseUrl = '';
  public innerWidth: any;
  size: SCREEN_SIZE;
  isMobile = false;
  isLoggedIn: boolean;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
  }
  constructor(private router: Router,
              private doRequest: DoRequestService,
              private resizeSvc: ResizeService) {
    this.baseUrl = environment.apiUrlBase;
    this.resizeSvc.onResize$
      .pipe(delay(0))
      .subscribe(x => {
        this.size = x;
        console.log(this.size);
        if (this.size <= 1) {
          this.isMobile = true;
        }
      });
  }

  ngOnInit(): void {
    //window.dispatchEvent(new Event('resize'));
    this.doRequest.getUser().subscribe( res  => {
      this.isLoggedIn = true;
      this.signedInUser = res as any;
    }, err => {

      this.isLoggedIn = false;
    });
  }

  toggleNav(): void {
    this.classApplied = !this.classApplied;
    console.log(this.classApplied);
  }

  goToHome() {
    this.router.navigate(['/']);
  }

  logout() {
    this.doRequest.logout().subscribe( res => {
      /*this.router.navigateByUrl('/refresh', { skipLocationChange: true }).then(() => {
        this.router.navigate(['/']);
      });*/

      window.location.reload();

    }, error => {
    });
  }

  reportAProblem(){
    alert('coming soon');
  }

}
