import { Component, OnInit, HostListener } from '@angular/core';
import { delay } from 'rxjs/operators';
import { ResizeService } from 'src/app/resizing-logic/resizing.service';
import { SCREEN_SIZE } from 'src/app/resizing-logic/screen-size.enum';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { LoginModalComponent } from 'src/app/login-modal/login-modal.component';
import { DoRequestService } from 'src/app/services/do-request.service';
import {IsLoggedInBoolService} from '../../services/IsLoggedInBoolService';
import {TranslocoService} from '@ngneat/transloco';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit {

  public innerWidth: any;
  size: SCREEN_SIZE;
  @HostListener('window:resize', ['$event'])
    onResize(event) {
    this.innerWidth = window.innerWidth;
  }

  isUserLoggedIn: boolean;

  constructor(private resizeSvc: ResizeService,
              public router: Router,
              public dialog: MatDialog,
              private doRequest: DoRequestService,
              private isLoggedInBoolService: IsLoggedInBoolService,
              public translationService: TranslocoService) {
    isLoggedInBoolService.myBool$.subscribe((newBool: boolean) => { this.isUserLoggedIn = newBool; });
    this.resizeSvc.onResize$
    .pipe(delay(0))
    .subscribe(x => {
      this.size = x;
      if (this.size <= 1) {
        // this.isGrid = true;
      }
    });


  }

  isChecked: any;

  ngOnInit(): void {

    console.log(this.router.url)
    console.log(localStorage.getItem('isEnglish'));

    if(localStorage.getItem('isEnglish') == 'true'){
      this.isChecked = false;
      this.translationService.setActiveLang('en');
    }else if(localStorage.getItem('isEnglish') == 'false') {
      this.isChecked = true;
      this.translationService.setActiveLang('es');
    }else {
      this.isChecked = false;
      this.translationService.setActiveLang('en');
    }

    this.doRequest.getUser().subscribe( r => {
      this.isUserLoggedIn = true;
    }, err => {
      this.isUserLoggedIn = false;
    });

    console.log(this.isChecked);
  }


  changeLanguage() {

    if(this.isChecked){
      this.isChecked = true;
      this.translationService.setActiveLang('es');
      localStorage.setItem('isEnglish','false');
    }else if(!this.isChecked){
      this.isChecked = false;
      this.translationService.setActiveLang('en');
      localStorage.setItem('isEnglish','true');
    }else {
      this.isChecked = true;
      this.translationService.setActiveLang('en');
      localStorage.setItem('isEnglish','false');
    }
  }
  openLoginModal() {
    if(this.isUserLoggedIn){

    }else {
      this.openL();
    }
  }

  openL() {
    const dialogRef = this.dialog.open(LoginModalComponent, {
      width: '800px',
      panelClass: 'custom-dialog-container'
    }).afterClosed().subscribe(result => {
      if ( result === 'isLoggedIn' ) {
        window.location.reload();
      }
    });
  }
  logout() {
    this.doRequest.logout().subscribe( res => {
      this.isLoggedInBoolService.myBool(false);
      window.location.reload();
      /*this.router.navigateByUrl('/refresh', { skipLocationChange: true }).then(() => {
        this.router.navigate(['/']);
    });*/

    }, error => {
      console.log(error);
    });
  }

  profile() {
    this.router.navigate(['/user']);
  }

  goToHome() {
    this.router.navigate(['/']);
  }

  goToSearch() {
    this.router.navigate(['/search-result']);
  }

  goToBlog() {
    this.router.navigate(['/blog']);
  }

  goToSell() {
    this.router.navigate(['add-product']);
  }

  goToAnnouncements() {
    this.router.navigate(['/user/announcements']);
  }

  goToFavourites() {
    this.router.navigate(['/user/favourites']);
  }

  goToNotifications() {
    this.router.navigate(['/user/notifications']);
  }

  goToAccount() {
    this.router.navigate(['/user/account']);
  }

  goToMessages() {
    this.router.navigate(['/user/messages']);
  }

}
