import { Component, OnInit } from '@angular/core';
import { DoRequestService } from 'src/app/services/do-request.service';
import { ResizeService } from 'src/app/resizing-logic/resizing.service';
import { delay } from 'rxjs/operators';
import { SCREEN_SIZE } from 'src/app/resizing-logic/screen-size.enum';
import {ActivatedRoute, Route, Router} from '@angular/router';
import {environment} from '../../../environments/environment';

@Component({
  selector: 'app-user-announcements',
  templateUrl: './user-announcements.component.html',
  styleUrls: ['./user-announcements.component.css']
})
export class UserAnnouncementsComponent implements OnInit {
  size: SCREEN_SIZE;
  isGrid: boolean;

  constructor(private doRequest: DoRequestService,
              private resizeSvc: ResizeService,
              private router: Router,
              private route: ActivatedRoute) {
                this.resizeSvc.onResize$
    .pipe(delay(0))
    .subscribe(x => {
      this.size = x;
      if (this.size <= 1) {
        this.isGrid = true;
      }
    });
              }
  pageNumber = 1;
  itemsPerPage = 5;
  numberOfRecords: number;
  numberOfPages: number;
  isLoading = false;
  orderDirection = 'DESC';
  allProducts: any;

  searchParameters = {
    user: true,
    searchString : '',
    brand : '',
    type : '',
    year : '',
    condition : '',
    radius : '',
    price : '',
    color : '',
    price_max : '',
    price_min : '',
    year_max : '',
    year_min : '',
    itemsPerPage : this.itemsPerPage,
    pageNumber : this.pageNumber,
    orderDirection : this.orderDirection
  };
  baseUrl = environment.apiUrlBase;
  ngOnInit(): void {
    this.performSearch();
  }

  performSearch() {

    this.isLoading = true;
    this.doRequest.performSearch(this.searchParameters).subscribe( res => {
      console.log(res);
      this.allProducts = res.data;
      this.numberOfRecords = res.numberOfRecords;
      this.numberOfPages = res.numberOfPages;
      console.log(res.highestPrice);
      // this.setNewFloorCeilPrice(res.lowestPrice , res.highestPrice);
      // this.setNewFloorCeilYear(res.lowestYear, res.highestYear);
      this.isLoading = false;
    }, err => {
      console.log(err);
      this.isLoading = false;
    });
  }

  editProduct(productId) {
    this.router.navigate([`user/edit-product/${productId}`]);
  }

  goToHome() {
    this.router.navigate(['/']);
  }

  delete() {
    alert('coming soon');
  }
}
