import {Component, HostListener, OnInit} from '@angular/core';
import {DoRequestService} from '../services/do-request.service';
import { Router } from '@angular/router';
import {environment} from '../../environments/environment';
import {SCREEN_SIZE} from '../resizing-logic/screen-size.enum';
import {ResizeService} from '../resizing-logic/resizing.service';
import {delay} from 'rxjs/operators';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {

  itemsPerPage = 5;
  pageNumber = 1;
  orderDirection = 'DESC';

  searchParameters = {
    searchString : '',
    itemsPerPage : this.itemsPerPage,
    pageNumber : this.pageNumber,
    orderDirection : this.orderDirection,
    category : ['17bac469-155c-474d-9bbd-d2df34ee42f1']
  };
  allPosts = [];
  numberOfRecords: any;
  numberOfPages: any;
  isLoading = false;
  baseUrl = environment.apiUrlBase;
  blogCategories:any;
  size: SCREEN_SIZE;
  public innerWidth: any;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
  }

  constructor(private doRequest: DoRequestService,
              private router: Router,
              private resizeSvc: ResizeService) {
    this.resizeSvc.onResize$
      .pipe(delay(0))
      .subscribe(x => {
        this.size = x;
        console.log(this.size);
      });
  }

  ngOnInit(): void {
   this.getPosts();
   this.getBlogCategories();
  }

  getPosts() {
    this.isLoading = true;
    this.doRequest.getPosts(this.searchParameters).subscribe( res => {
      this.allPosts = res.data;
      this.numberOfRecords = res.numberOfRecords;
      this.numberOfPages = res.numberOfPages;
      this.isLoading = false;
    }, err => {
      console.log(err);
      this.isLoading = false;
    })
  }

  next() {
     if (this.searchParameters.pageNumber !== this.numberOfPages) {
       this.searchParameters.pageNumber += 1;
       this.pageNumber += 1;
       this.getPosts();
     } else {
       console.log('No more records');
     }
  }

  previous() {
    if (this.pageNumber !== 1) {
      this.searchParameters.pageNumber -= 1;
      this.pageNumber -= 1;
      this.getPosts();
    }
  }

  viewPost(post) {
    console.log(post);
    this.router.navigate([`/post/${post.id}`]);
  }

  getBlogCategories() {
    this.doRequest.getBlogCategories().subscribe(res => {
      this.blogCategories = res;
    }, error => {

    })
  }

  changeCategory(event: any) {
    this.searchParameters.category = event.target.value;
    this.getPosts();
  }

  changeSearchString(event: any) {
    this.searchParameters.searchString = event.target.value;
    this.getPosts();
  }

}
