import {Component, HostListener, OnInit} from '@angular/core';

import {delay} from 'rxjs/operators';
import {SCREEN_SIZE} from '../../resizing-logic/screen-size.enum';
import {ResizeService} from '../../resizing-logic/resizing.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-not-found',
  templateUrl: './not-found.component.html',
  styleUrls: ['./not-found.component.css']
})
export class NotFoundComponent implements OnInit {

  public innerWidth: any;
  size: SCREEN_SIZE;
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
  }
  constructor(private resizeSvc: ResizeService,private router: Router) {
    this.resizeSvc.onResize$
      .pipe(delay(0))
      .subscribe(x => {
        this.size = x;
        if (this.size <= 1) {
        }
      });
  }

  ngOnInit(): void {
  }

  goToSearch() {
    this.router.navigate(['/search-result']);
  }
}
